___CSS_LOADER_EXPORT___.push([module.id, `body{
    font-size: 2cap;
    font-family: 'Open Sans', arial;
    background-color: #2f2c3d;
}

h1{
    display: block;
    text-align: center;
    width: 100%;
}

.conteiner{
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    background-color: aliceblue;
    max-width: 600px;
    margin: 60px auto;
    border-radius: 10px;
    padding: 10px;
}

form{
    line-height: 40px;
}

input{
    font-size: 18px;
    height: 20px;
    width: 10cm;
}

form button{ 
    height: 30px;
    margin-bottom: 10px;
}

input:focus, input:hover{
    outline:1px solid #333042;
}


button{
    border: 0;
    cursor: pointer;
    background-color: #2f2c3d;
    color: aliceblue;
    height: 40px;
    font-size: 15pt;
}

button:hover{
    background-color: #333042;
}

div{
    text-align: center;
    font-size: 1em;
    color: #2f2c3d;
    font-weight: bold;
}
